var add0 = function(m) {
	return m < 10 ? '0' + m : m;
};
var dateFormat = function(timestamp) {
	var time = new Date(timestamp * 1); //DateDate
	var year = time.getFullYear(),
		month = time.getMonth() + 1, //0
		day = time.getDate(),
		hour = time.getHours(),
		minute = time.getMinutes(),
		second = time.getSeconds();
	//add0()
	return (
		year +
		'-' +
		add0(month) +
		'-' +
		add0(day) +
		' ' +
		add0(hour) +
		':' +
		add0(minute) +
		':' +
		add0(second)
	);
};

export default dateFormat;
